export default function (minutes, formatHour = ' giờ', formatMinute = ' phút', isSplitLine = false) {
    if (!minutes) minutes = 0

    let splitLine = isSplitLine ? '<br />' : ' '

    if (minutes < 60) return splitLine + minutes + formatMinute

    let hours  = Math.floor(minutes / 60)
    let extraMinutes = minutes % 60

    if (extraMinutes == 0) return hours + formatHour
    else return hours + formatHour + splitLine + extraMinutes + formatMinute
}